<template>
  <!-- 新闻 -->
  <div class="news">
    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <span class="card-title">{{ $t('commom.news') }}</span>
      </div>
      <el-row style="text-align:center">
        <div v-for="(item,index) in list" :key="index" class="text item">
            <p style="text-decoration: underline;" @click="target(index)">{{item.title}}</p>
        </div>
      </el-row>
    </el-card>
  </div>
</template>

<script>
import listZh from '../../static/news-zh'
import listEn from '../../static/news-en'
export default {
  name: 'News',
  data () {
    return {
    }
  },
  computed: {
    list () {
      return this.$i18n.locale === 'en' ? listEn : listZh
    }
  },
  methods: {
    target (index) {
      this.$store.commit('news/selectIndex', index)
      this.$router.push('/news/newsDetail')
    }
  }
}
</script>

<style lang="scss" scoped>
@import "../assets/variables.scss";
.news {
  .text {
    font-size: 16px;
  }
  .item {
    margin-bottom: 18px;
  }
  .clearfix:before,
  .clearfix:after {
    display: table;
    content: "";
  }
  .clearfix:after {
    clear: both;
  }

  .box-card {
    a {
      border-radius: 4px;
      color: #ddc597;
      font-weight: 700;
      word-break: break-all;
    }
  }
}
</style>
<style scoped>
.el-card {
  margin-top: 1.5rem;
  background-color: #252323;
  border: 0px solid #252323;
  color: #dac28f;
}
.box-card >>> .el-card__header {
  padding: 8px 20px;
  background: linear-gradient(to bottom, #efdbca, #fdd650);
  font-size: 16px;
  border: 0px solid #f9d76c;
}
</style>



